// width height
@each $property, $value in $spaces {
    .w-#{$property} {
      width: $value !important;
    }
}

@each $property, $value in $spaces {
    .h-#{$property} {
      height: $value !important;
    }
}

.min-h {
  &-252 {
    min-height: 252px;
  }
  &-inherit {
    min-height: inherit;
  }
}
